import React from 'react';
import { projectNames } from '../../helpers/projects';
import { projectContent } from '../../helpers/projectContent';

const Projects = () => {

  const renderProjects = () => {
      const project = projectNames.map((proj) => {
        const projectPascalCase = proj.replace(/\s/g, '');
        const content = projectContent[projectPascalCase].content;
        const imgURL = content.tileBackgroundImg;

        return (
          <a href={`/projects/${proj}`}><div className="Projects__category-tileBoundingBox"><div key={proj} className="Projects__project-category-tile"><img src= {imgURL}/><span>{proj}</span></div></div></a>);
      });

    return (
      <div className="Projects__projects">
        {project}
      </div>
    );
  }

  return (
    <div className="Projects__wrapper">
      <div className="Projects__projects-list">
        {renderProjects()}
      </div>
    </div>
  );
}

export default Projects;


