import React from 'react';
import PropTypes from 'prop-types';
import { projectNames } from '../../helpers/projects';
import { projectContent } from '../../helpers/projectContent';

const Project = (props) => {
  const {
    params
  } = props.match;

  let selectedProject = '';

  projectNames.forEach((project) => {
    if (project === params.project) {
      selectedProject = params.project;
    }
  });

  if (selectedProject === '') {
    return (<div>Error! This project doesn't exist</div>);
  }

  const selectedProjectPascalCase = selectedProject.replace(/\s/g, '');
  const content = projectContent[selectedProjectPascalCase].content;

  return (
    <div className="Project__wrapper">
      <div className="Project__header">{selectedProject}</div>
      <div className="Project__descriptions">
        {content.descriptions.map((description, i) =>
          <div className="Project__descriptions" key={i} dangerouslySetInnerHTML={{__html: description}}></div>
        )}
      </div>
      {content.imgs &&
        <div className="Project__images">
          {content.imgs.map((img, i) =>
              <div className="Project__image" key={i}><img height="auto" width="300" src={img}></img></div>)
          }
        </div>
      }
      {content.iframeSrc &&
        <div className="Project__video">
            <iframe width="560" height="315" title={selectedProjectPascalCase} src={content.iframeSrc} frameBorder="0" allowFullScreen className="Project__iframe"></iframe>
        </div>
      }
    </div>
  );
}

Project.propTypes = {
  match: PropTypes.shape({
    params: PropTypes.shape({
      project: PropTypes.string
    })
  })
}

Project.defaultProps = {
  match: {
    params: {
      project: ''
    }
  }
}

export default Project;


