import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router'

class Header extends Component {

  componentDidMount() {
    const path = this.props.location.pathname;

    if (path === '/') {
      document.getElementById("home-link").classList.add("selected");
    } else if (path.includes("projects")) {
      document.getElementById("projects-link").classList.add("selected");
    } else if (path.includes("contact")) {
      document.getElementById("contact-link").classList.add("selected");
    }
  }

  render() {
    return (
      <div className="Header">
        
        <div className="Header__logo">
          <a className="nav-link" id="home-link" href="/"><div className="Header__logo--Container"></div></a>
        </div>
        
        <div className="Header__navigation">
          <div className="Header__navigation--links">
        		<div className="Header__navigation--link" id="projects-link"><a className="nav-link" href="/projects">Projects</a></div>
        		<div className="Header__navigation--link" id="contact-link"><a className="nav-link" href="mailto:helgesonse@gmail.com?subject=Contact%20from%20stuhelgeson.com">Contact</a></div>
          </div>
        </div>
      </div>
    );
  }
}

Header.propTypes = {
  location: PropTypes.object
}

Header.defaultProps = {
  location: {}
}
// <img className="logo" src="/images/logo3.png" />
//<div className="Header__navigation--link" id="home-link"><a className="nav-link" href="/">Home</a></div>
export default withRouter(Header);



