			<p>My role on the team involved creating a portable data collection rig. I was responsible for designing the hand held structure that held a camara, various robotic end effectors, force sensors, and positioning sensors. The device also had a backpack component that housed the power and computing systems. I ran tests while developing the backpack component to ensure the system wouldnt overheat when operated. </p>
