import React, { Component } from 'react';
// import { Email, Item, Span, A, renderEmail } from 'react-html-email'

class ContactPage extends Component {

	constructor() {
        super();
        this.state = {
    	   name: '',
    	   email: '',
    	   message: ''
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({
    	   name: event.target.value
        });
    }

	handleSubmit(event) {
    event.preventDefault();
  }

	render() {
//		const emailHTML = renderEmail(
//		  <Email title="Hello World!">
//		    <Item align="center">
//		      <Span fontSize={20}>
//		        This is an example email made with:
//		        <A href="https://github.com/chromakode/react-html-email">react-html-email</A>.
//		      </Span>
//		    </Item>
//		  </Email>
//		);

	  return (
	    <div className="ContactPage">
	    	<div className="ContactPage__wrapper">
		    	<div className="ContactPage__header">
		    		Get in touch!
		    	</div>
		    	<form className="ContactPage__form" action="sendFormEmail" method="POST">
                    <label>Name</label>
                    <input className="ContactPage__input-text" name="name"  type="text" placeholder=" First and Last Name" required/>
	                <label>Email</label>
	                <input className="ContactPage__input-text" name="email" type="email" placeholder=" example@example.com" required/>
	                <label>Message</label>
	                <textarea rows="5" className="ContactPage__input-text" name="message" type="text" placeholder=" Type your message here."/>
		            <input className="ContactPage__submit-button" name="submit" type="submit" value="Send"/>
		      </form>
	    	</div>
	    </div>
	  );
	}
}

export default ContactPage;


